import React, {Component} from "react";
import "./index.css"
import {connect} from "react-redux";

import { verificationActionHelper } from "../../../store/actions.js"


class RegistrationVerification extends Component {
    constructor(props) {
        super(props);

        this.state = {
            submitted: false,
            email: "",
            validationCode: 0,
            username: "",
            password: "",
            password_repeat: "",
            location: "",
        };
    }

    handleEmail = e => {
        const email = e.currentTarget.value;
        this.setState({ email });
    };

    handlePassword = e => {
        const password = e.currentTarget.value;
        this.setState({ password });
    };

    handlePasswordRepeat = e => {
        const password_repeat = e.currentTarget.value;
        this.setState({ password_repeat });
    };

    handleValidation = e => {
        const code = e.currentTarget.value;
        this.setState({ code });
    };

    handleUsername = e => {
        const username = e.currentTarget.value;
        this.setState({ username });
    };

    handleLocation = e => {
        const location = e.currentTarget.value;
        this.setState({ location });
    };

    handleSubmit = e => {
        e.preventDefault();
        this.setState({ submitted: true });
        this.props.dispatch(verificationActionHelper(this.state))
    };

    render() {
        return (
            <div className="form-wrapper">
                <h2>Verification</h2>
                <div className="title-underline"> </div>
                {!this.state.submitted ?
                    <form className="verification-form-style" onSubmit={this.handleSubmit}>
                    <input
                        className="input-style"
                        type="email"
                        placeholder="E-Mail address"
                        onChange={this.handleEmail}
                    />
                    <input
                        className="input-style"
                        type="number"
                        placeholder="Validation code"
                        onChange={this.handleValidation}
                    />
                    <input
                        className="input-style"
                        type="text"
                        placeholder="Username"
                        onChange={this.handleUsername}
                    />
                    <input
                        className="input-style"
                        type="text"
                        placeholder="Location"
                        onChange={this.handleLocation}
                    />
                    <input
                        className="input-style"
                        type="password"
                        placeholder="Password"
                        onChange={this.handlePassword}
                    />
                    <input
                        className="input-style"
                        type="password"
                        placeholder="Password repeat"
                        onChange={this.handlePasswordRepeat}
                    />
                    <input
                        type="submit"
                        value="Finish registration"
                        className="submit-form"/>
                </form> :
                    <p>Thanks for registering.</p> }
            </div>
        )
    }
}

const mapStateToProps = state => ({
    state
});

export default connect(mapStateToProps)(RegistrationVerification)


