import React, { Component } from "react";
import "./index.css"
import { connect } from "react-redux";
import { registerActionHelper } from "../../store/actions.js"
import {loginActionHelper} from "../../store/actions";

// todo figure out how to display message if email already exists

class Registration extends Component {
   constructor(props) {
    super(props);

    this.state = {
      submitted: false,
      email: ""
    };
  }

  handleEnterEmail = e => {
    const email = e.currentTarget.value;
    this.setState({ email });
  };

   handleSubmit = e => {
       e.preventDefault();
       if (this.state.email) {
           this.setState({ submitted: true, email: this.state.email})
       }
       console.log(this.props);
       this.props.dispatch(registerActionHelper(this.state));
   };

   // handleSubmit = e => {
   //      e.preventDefault();
   //      this.props.dispatch(loginActionHelper(this.state)).then((data) => {
   //          if (data) {
   //              if (data.username) {
   //                alert(`Username: ${data.username}`);
   //              } else if (data.password) {
   //                  alert(`Password: ${data.password}`);
   //              } else if (data.non_field_errors) {
   //                  alert(`Field: ${data.non_field_errors}`);
   //              }
   //          } else {
   //              this.props.history.push("/")
   //          }
   //
   //      })
   //  };

    render() {
        return (
            <div className="form-wrapper">
                <h2>Registration</h2>
                <div className="title-underline"> </div>
                {!this.state.submitted ?
                <form onSubmit={ this.handleSubmit } className="form-style">
                    <input
                        type="email"
                        name="email"
                        onChange={ this.handleEnterEmail}
                        placeholder="E-Mail Address"
                        className="input-style" />
                    <input type="submit" value="Register" className="submit-form"/>
                </form>
                :
                <p className="monkey-text">
                  Thanks for your registration.
    Our hard working monkeys are preparing a digital message called E-Mail that will be sent to you soon. Since monkeys
                    aren't good in writing the message could end up in you junk folder. Our apologies for any
                    inconvenience.
                </p>
                }
                </div>
        )
    }
}


const mapStateToProps = state => ({
  state
});

export default connect(mapStateToProps)(Registration);


